import "react-widgets/styles.css";

function App() {
  return (
    
    <div className="container">
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Index></Index>}></Route>
          <Route path="/country/:id" element={<Country></Country>}></Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
