import React,{useEffect, useState} from "react";
import { getCountries } from "../functions/functions";
import Combobox from "react-widgets/Combobox";

const Index = () => {
    const [countries, setCountries] = useState([])
    const [country, setCountry] = useState(null)

    useEffect(() => {
        getCountries(setCountries)
    }, [])

    const displayInfo = (country) => {
        if(country instanceof Object){
            setCountry(country);
        }
    }

    return (
        <div>
            <div className="mb-3">
                <Combobox 
                    data={countries}
                    dataKey="name"
                    textField="name"
                    placeholder="Selecciona un país"
                    onChange={displayInfo}
                />
            </div>
            <>
                {country != null ? (
                    <div>
                        <h1>{country.name}</h1>
                        <div className="row">
                            <div className="col">
                                <p>Capital: {country.capital}</p>
                            </div>
                            <div className="col">
                                <p>Región: {country.region}</p>
                            </div>
                            <div className="col-3 p-3" style={{background: "lightgray", border: "darkgray", borderStyle: "solid"}}>
                            {/* // "
                              //  background: lightgray;
                               // border: darkgray;
                            //"> */}
                                <h5 className="text-center">Bandera</h5>
                                <img src={country.flags.svg} className="w-100" />
                            </div>
                        </div>
                    </div>
                    //(Nombre, Código, Capital, Región, etc) y su bandera
                ) : ("")}
            </>
        </div>
    )
}
export default Index